ent:space-between}.canvas-menu-container span{width:1px;height:100%;background-color:gainsboro}.canvas-menu-img{width:20px;height:20px;cursor:pointer;transition-duration:0.2s;transition:ease}.canvas-menu-img:hover{transform:scale(1.1)}#canvas-container{width:100%;height:100%}#myCanvas{background-image:url("data:image/svg+xml;base64,");width:100%;height:100%;border:1px solid gainsboro}.modal-main-container{position:absolute;z-index:1;background-color:rgba(0,0,0,0.4);height:100%;width:100%;display:flex;justify-content:center;align-items:center}.modal-container{width:25%;min-height:20%;background-color:white;border-radius:6px;display:flex;flex-direction:column;justify-content:space-between}.modal-title-container{padding:10px 10px;display:flex;justify-content:space-between;border-bottom:1px solid gainsboro}.modal-close{color:gainsboro;cursor:pointer}.modal-close:hover{color:#c2c1c1}.modal-input{margin:0 24% 0 3%;border:1px solid gainsboro;border-radius:3px;padding:5px 10px}.modal-input:focus{outline:none;border:1px solid #c2c1c1}.modal-submit{max-width:18%;font-family:inherit;font-weight:700;background:lightgray;margin-left:78%;margin-bottom:3%;background:#FBFBFB;border:1px solid gainsboro;padding:5px;border-radius:3px;cursor:pointer}.modal-submit:hover{background-color:#c2c1c1;color:white}#info-sidebar{margin-top:3.4%;padding:10px;background:#0000001a;border-radius:3px;margin-left:12px;width:18%;height:fit-content;margin-right:10px}.top-bar-container{display:flex}.info-title{width:95%;text-align:center}.info-close{cursor:pointer}.info-body{margin-top:2%}.info-body p{margin-top:7px;font-size:0.9em;font-family:auto}
